.calculatorPadCell {
	position: absolute;
	padding-bottom: 43%;
	padding-top: 43%;
	text-align: center;
	font-size: 5vw;
	max-width: 135px;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
}
.calculatorPadCell1 {
	font-size:  12vw;
}

.calculatorPadCell2 {
	font-size:  10vw;
}

.calculatorPadCell3 {
	font-size:  10vw;
}

.calculatorPadCell4 {
	font-size: 7vw;
}
@media all and (min-width: 700px) {
	.calculatorPadCell1 {
		font-size:  86px;
	}

	.calculatorPadCell2 {
		font-size: 50px;
	}

	.calculatorPadCell3 {
		font-size:  60px;
	}

	.calculatorPadCell4 {
		font-size: 50px;
	}
}

@media all and (min-width: 699px) and (max-width: 288px) {
	.calculatorPadCell1 {
		font-size:  12vw;
	}

	.calculatorPadCell2 {
		font-size:  10vw;
	}

	.calculatorPadCell3 {
		font-size:  10vw;
	}

	.calculatorPadCell4 {
		font-size: 7vw;
	}
}


